.Info {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    justify-content: space-between;
    margin-top: 40px;
    width: 100%;
}

.infoBlocks {
    display: flex;
    justify-content: space-between;
    width: 100%;
    .infoBlock {
        width: 25%;
        background: #ffffff;
        box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        padding: 2.6vw;

        cursor: pointer;

        justify-content: space-between;
        margin-bottom: 40px;

        .link {
            cursor: pointer;
            margin-top: 0.5em;
        }

        .title {
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 600;
            font-size: 1.9rem;

            color: #000000;
        }

        .text {
            font-family: "Montserrat";
            font-style: normal;
            font-weight: normal;
            font-size: 1rem;
            line-height: 20px;
            color: #000000;
            margin: 8px 0px;
            min-height: 3.5em;
        }
    }
}

.participateBlocks {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-around;
    justify-content: center;
    width: 100%;

    .participateBlock {
        width: 25%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 20px;
        margin-right: 20px;
    }

    .participateBlock::after {
        content: "";
        position: absolute;
        background-color: rgb(226, 224, 224);
        height: 2px;
        width: 25%;
        margin-top: 4%;
        margin-left: 15%;
        z-index: -1;
    }

    .participateBlock:last-child::after {
        display: none;
    }

    .imgBlock {
        display: flex;
        flex-direction: row;
        justify-content: center;
        height: 9vw;
    }

    .title {
        font-family: "Montserrat";
        text-align: center;
        font-style: normal;
        font-weight: 600;
        font-size: 16pt;
        line-height: 29px;
        color: #000000;
        margin-bottom: 10px;
    }

    .text {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: normal;
        font-size: 12pt;
        line-height: 24px;
        text-align: center;
        color: #000000;
        margin-bottom: 10px;
    }

    .link {
        --b: 2px;
        --r: 10px;

        background: -webkit-linear-gradient(#0aa7f5, #3ce7ff);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        color: transparent;

        border-radius: var(--r);
        display: flex;
        align-items: center;
        justify-content: center;
        font: 15pt 'Montserrat';
        font-weight: 500;

        margin: 0 auto;
        position: relative;
        z-index: 0;
        text-decoration: none;

        width: 15em;
        padding-top: 10px;
        padding-bottom: 10px;

        margin-top: 10px;
     
        cursor: pointer
    }

    .link::before {
        content:"";
        position:absolute;
        z-index:-1;
        top:0;
        left:0;
        right:0;
        bottom:0;
        border-radius: var(--r);
        border: var(--b) solid transparent;
        border-radius: var(--r);
        background: inherit;
        background-origin:border-box;
        background-clip:border-box;
        -webkit-mask:
          linear-gradient(#fff 0 0) padding-box,
          linear-gradient(#fff 0 0);
        -webkit-mask-composite:destination-out;
        mask-composite: exclude;
        -webkit-mask-repeat:no-repeat;
      }
}

.titleBlock {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 49px;
    color: #000000;
    text-align: center;
    width: 100%;
    margin-top: 36px;
    margin-bottom: 40px;
}

@media (max-width: 900px) {
    .participateBlocks {
        flex-wrap: wrap;
    }
}

@media (min-width: 350px) {
    .participateBlocks {
        justify-content: space-between;
        .participateBlock {
            width: 100%;
        }
    }
}

@media (max-width: 1280px) {
    .infoBlocks {
        .infoBlock {
            width: 26.3% !important;
            max-width: none;
        }
    }
}

@media (max-width: 1440px) {
    .infoBlock {
        .title {
            font-size: 18pt;
        }
        .text {
            font-size: 9pt;
        }

        .link {
            margin-top: 0;
        }
    }

    .participateBlocks {
        .participateBlock {
            .title {
                font-family: "Montserrat";
                text-align: center;
                font-style: normal;
                font-weight: 600;
                font-size: 12.5pt;
                line-height: 14pt;
                color: #000000;
            }

            .text {
                font-family: "Montserrat";
                font-style: normal;
                font-weight: normal;
                font-size: 10pt;
                line-height: 13pt;
                text-align: center;
                color: #000000;
            }

            .link {
                font-family: "Montserrat";
                font-style: normal;
                font-weight: 600;
                font-size: 12.5pt;
                line-height: 16pt;
                text-align: center;
                background: -webkit-linear-gradient(#0aa7f5, #3ce7ff);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                cursor: pointer;
            }
        }
    }
}

@media (max-width: 1000px) and (orientation: portrait) {
    .Info {
        flex-direction: column;
        .infoBlocks {
            flex-direction: column;
            .infoBlock {
                padding: 1.5em;
                width: auto !important;
                max-width: none;
                margin-bottom: 0.9em;
            }
        }

        .titleBlock {
            margin-top: 2em;
            font-size: 20pt;
            font-weight: 900;
            margin-bottom: 0.2em;
        }

        .participateBlocks {
            .participateBlock {
                margin-bottom: 2em;
            }
            .imgBlock {
                height: 11em;
            }
            .participateBlock::after {
                display: none;
            }
        }
    }
}
